.button {
  display: block;
  margin: 0;
  border: none;
  border-radius: 3px;
  cursor: pointer;

  font-family: $roboto-condensed;
  font-size: 14px;
  font-weight: 700;
  line-height: 14px;
  letter-spacing: 0.05em;
  text-align: center;
  text-transform: uppercase;
}
