.features__list {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 31px 30px;

  @include vp-1023 {
    gap: 30px 29px;
  }

  @include vp-767 {
    grid-template-columns: 1fr;
    gap: 30px 0;
  }

}

.features__item {
  h3 {
    margin: 20px 0 16px;

    font-family: $roboto-condensed;
    font-size: 24px;
    font-weight: 400;
    color: $color-deep-sky-blue;
    line-height: 34px;

    @include vp-767 {
      font-size: 20px;
      line-height: 28px;
    }
  }

  p {
    margin: 0;
    
    color: $color-default-black;
    line-height: 21.6px;

    @include vp-767 {
      font-size: 14px;
      line-height: 18.9px;
    }
  }

  svg {
    color: $color-deep-sky-blue;
  }

  &:hover {
    background-color: $color-deep-sky-blue;

    h3,
    p,
    svg {
      color: $color-alice-blue;
    }
  }
}

.features__item {
  max-width: 595px;
  min-height: 300px;
  padding: 40px 44px;
  border: 1px solid $color-whisper;
  box-shadow: inset 5px 0 $color-deep-sky-blue;

  @include vp-1023 {
    min-height: 360px;
    padding-bottom: 26px;
  }

  @include vp-767 {
    min-width: 270px;
    min-height: 340px;
    padding: 30px 30px 35px;
  }
}
